@charset "UTF-8";
.usc-callout {
  display: block;
  margin-bottom: var(--spacing-component);
  border: 3px solid var(--border-secondary-dark);
  border-radius: 20px;
  background: var(--surface-body);
}

.usc-callout__title {
  margin: 0;
  padding: 20px 25px;
  border-radius: 10px 10px 0 0;
  background-color: var(--border-secondary-dark);
  font-weight: bold;
  font-size: 22px;
  color: var(--text-on-surface-secondary-dark);
}

.usc-callout__content {
  padding: 20px 25px;
}

.usc-callout__body :first-child {
  margin-top: 0;
}
.usc-callout__body :last-child {
  margin-bottom: 0;
}

.usc-callout__attribution {
  margin-top: var(--spacing-md-1);
  font-style: italic;
}
.usc-callout__attribution:before {
  content: "â€”";
}